body,html,div,h1,h2,h3,h4,h5,h6,p,span,a,ul,li,ol,img,small,textarea{
	margin: 0;
	padding: 0;
	text-decoration: none;
	list-style: none;

}
body,html{
	background: #e5e8ec;
	/* background:#fff; */
}
.user2{
	background:#fff;
	overflow: hidden;
	padding: 0.4rem;
}
.user2 img{
	border-radius: 50%;
	float: left;
	margin-right: 0.3rem;
	width: 1rem;
	height: 1rem;
}
.username2{
	float: left;
	max-width: 76%
}
.username2 h2{
	margin-bottom: 0.2rem;
	font-size: 0.4rem;
}
.username2 span{
	font-size: 0.26rem;
}
.username2 span:nth-of-type(3){
	color: #777;
}
.content2{
	background:#fff;
	padding: 0.4rem;
	font-size: 0.3rem;
	line-height: 1.8;
	text-align: justify;
	margin-top: 0.02rem;
}
.operation{
	background:#f4f5f7;
	padding:0.2rem 0.2rem 0.2rem 0.4rem;
	overflow: hidden;
}
.operation span{
	font-size: 0.28rem;
	padding-right: 0.2rem;
}
.share{
	float: left;
}
.agree{
	float: right;
}

.screen2{
	background:#fff;
	margin-top: 0.2rem;
}

.comm-num p{
	padding:0.16rem 0.4rem;
	font-size: 0.28rem;
	border-bottom: 0.02rem solid #d9d9d9;
}
							/* 讨论者的回复 */
.item{
	overflow: hidden;
	border-bottom:0.02rem solid #d9d9d9;
}
.comm2{
	position: relative;
	background:#fff;
	margin-top: 0.04rem;
	overflow: hidden;
	padding: 0.4rem 0rem 0.15rem 0.4rem;
}
.comm2 img{
	border-radius: 50%;
	float: left;
	margin-right: 0.2rem;
	width: 0.8rem;
	height: 0.8rem;
}
.guest2{
	width:100%;
	font-size: 0.28rem;
	line-height: 1.5;
}
.guest2 h5{
	text-align: left;
}
.guest2 p{
	font-size: 0.26rem;
	padding-bottom: 0.4rem;
	text-align: justify;
}
.guest2 span{
	color: #777;
	font-weight:100;
}
.reply{
	position: absolute;
	right:0.4rem;
	top:0.36rem;
	float: right;
}
.reply span{
	color:#777;
	font-size: 0.26rem;
	margin-left: 0.26rem;
}
.big{
	float:left;
	width:80%;
	word-break: break-all;
	text-align: justify;
}
					/* 讨论者的被回复 */

.more-peo{
	position: relative;
	padding: 0.3rem 0rem;
	overflow: hidden;
	border-top:0.02rem solid #d9d9d9;
}
.more-peo img{
	float: left;
	width: 0.8rem;
	height: 0.8rem;
	border-radius:50%;
	margin-right: 0.2rem;
}
.more-peo>p{
	float: left;
	margin-top: 0.05rem;
	max-width:78%;
	min-width: 27%;
	font-size: 0.28rem;
	padding-right: 0.2rem;
	text-align: justify;
}
.another{
	font-size: 0.26rem;
	color:#777;
	float: left;
	width: 55%;
}

.another span{
	color:#000;
	font-weight: 600;
}
.reply-two{
	position: absolute;
	right:0.2rem;
	top:0.2rem;
	float: right;
}
.reply-two span{
	color:#777;
	font-size: 0.26rem;
	margin-left: 0.26rem;
}
								/* 添加评论 */
.screen3{
	margin-top: 0.2rem;
	margin-bottom: 0.4rem;
}
.add-comm{
	padding:0.15rem 0.4rem;
	font-size: 0.28rem;
	border-bottom: 0.02rem solid #d9d9d9;
	background:#fff;
}
.text1{
	overflow: hidden;
}
textarea{
	width: 97%;
	height: 3rem;
	border: none;
	outline: none;
	font-size: 0.32rem;
	color:#555;
	padding: 0.15rem;
	resize:none;
}
.btn-b{
	width: 100%;
	padding: 0.15rem 0rem;
	font-size: 0.32rem;
	background:#0071eb;
	color:#fff;
	border: none;
	margin-top: 0.1rem;
}



