body,html,div,h1,h2,h3,h4,h5,h6,p,span,a,ul,li,ol,img,small,textarea{
	margin: 0;
	padding: 0;
	text-decoration: none;
	list-style: none;

}
	

body,html{
	background: #e5e8ec;
	height: 100%;
	
}
.top{
	background:#fff;
	padding: 0.2rem 0.4rem;
	font-size: 0.28rem;
	margin-bottom: 0.04rem;
	text-align: left;
}
.container{
	margin-bottom: 0.3rem;

}
.screen{
	background:#fff;
	padding: 0.4rem;
	overflow: hidden;
}
.user{
	overflow: hidden;
}
.user img{
	border-radius: 50%;
	float: left;
	margin-right: 0.2rem;
	width:0.8rem;
	height: 0.8rem;
}
.username{
	font-size: 0.28rem;
	float: left;
	line-height: 1.5;
	text-align: left;
	width: 82%;
}
.username p:nth-of-type(2){
	color:#777;
}
.artical{
	margin-top: 0.2rem;
	font-size: 0.32rem;
	text-align: left;
}
.artical h4{
	margin-bottom: 0.12rem;
}
.artical p{
	line-height: 1.3;
}
.comm{
	background:#fff;
	margin-top: 0.04rem;
	overflow: hidden;
}
.comm img{
	border-radius: 50%;
	float: left;
	margin:0.4rem 0.2rem 0.4rem 0.4rem;
	width:0.8rem;
	height: 0.8rem;
}
.guest{
	font-size: 0.28rem;
	line-height: 1.5;
	float: right;
	width: 73%;
	padding-right: 0.28rem;
	text-align: justify;
	word-break: break-all;
}
.guest h5{
	font-size: 0.28rem;
	margin-top: 0.4rem;
}
.guest p{
	font-size: 0.28rem;
	padding-bottom: 0.15rem;
}
.guest span{
	color: #555;
	font-weight:100;
}
.others{
	background:#fff;
	width: 100%;
	margin-top: 0.04rem;
	display:block;
}
.others p{	
	padding: 0.16rem 0.4rem;
	font-size: 0.28rem;
}


