.am-list-body .am-list-item:last-child{
margin-bottom: 0.2rem;
}

.am-list-item .topxian{
	text-align: center;
}
.am-list-item .topxian>img{
	width: 1.4rem;
	height: 1.4rem;
	border-radius: 50%;
	margin-left: center;
}

.inf{
	background:#fff;
	padding: 0.3rem;
	margin-bottom: 0.02rem;
	font-size: 0.28rem;
	overflow: hidden;
}


.inf img{
	width: 0.6rem;
	height: 0.6rem;
	border-radius:50%;
	margin-right: 0.1rem;
	vertical-align: middle;
	float: left;
}

.abcd{
	float: right;
	width: 87%;
	padding-top: 0.1rem;
}
.abcd span{
	margin-right: 0.1rem;
	display: inline-block;
	margin-bottom: 0.08rem;
	word-break: break-all;
}
.col{
	color: #777
}
.b{
	font-weight: bold;
}


